
.water-test-timer-button {
    background-color: white;
    color: black;
    border: 2px solid #4CAF50;
    height: 50px;
    width: 150px;
    font-size: large;
    margin: 15px;
}

.water-test-timer-button:hover {
    background-color: #4CAF50;
    color: white;

}
.water-param-instructions-container {
    position: absolute;
    top: 0;
    right: 40px;
    border: blueviolet 1px solid;
    border-radius: 30px;
    padding: 10px;

}

.water-param-instructions {
    border-radius: 30px;
    padding: 10px 10px 10px 25px;

}

.ammonia {
    background-image: linear-gradient(#f6f2aa, #58c32e)
}
.nitrite {
    background-image: linear-gradient(#86f0f5, #ce54e8)
}
.nitrate {
    background-image: linear-gradient(#f5ef80, #f14b50)
}
.ph {
    background-image: linear-gradient(#f0f488, #1becf4)
}
.highrangeph {
    background-image: linear-gradient(#fbc775, #976ef9)
}
